import { ethers } from "ethers";
import { getAddresses } from "../../constants";
import { meowContractABI } from "../../abi";
import { setAll } from "../../helpers/set-all";
import {
  createSlice,
  createSelector,
  createAsyncThunk,
} from "@reduxjs/toolkit";
import { JsonRpcProvider } from "@ethersproject/providers";
import { RootState } from "../../state";
import axios from "axios";

interface ILoadGameDetails {
  networkID: number;
  provider: JsonRpcProvider;
}

export const loadGameDetails = createAsyncThunk(
  "app/loadGameDetails",
  //@ts-ignore
  async ({ networkID, provider }: ILoadGameDetails) => {
    const addresses = getAddresses(networkID);
    const meowContract = new ethers.Contract(
      addresses.MEOW_ADDRESS,
      meowContractABI,
      provider
    );
    
    let gameData: any[] = [];
    let randomData: any[] = [];
    let resultData: any[] = [];
    let winnerData: any[] = [];

    await axios.get(`http://35.183.204.238/api/betting`).then((res) => {
      gameData = res.data;
    });
    await axios.get(`http://35.183.204.238/api/random`).then((res) => {
      randomData = res.data;
    });
    await axios.get(`http://35.183.204.238/api/result`).then((res) => {
      resultData = res.data;
    });
    await axios.get(`http://35.183.204.238/api/winner`).then((res) => {
      winnerData = res.data;
    });

    const gameprice = (
      (await meowContract.gamePrice()) / Math.pow(10, 18)
    ).toString();
    const jackpotAmount = (
      (await meowContract.jackpotAmount()) / Math.pow(10, 18)
    ).toString();

    return {
      gameprice,
      jackpotAmount,
      gameData,
      randomData,
      resultData,
      winnerData,
    };
  }
);

const initialState = {
  loading: true,
};

export interface IAppSlice {
  gameprice: string;
  jackpotAmount: string;
  gameData: any[];
  allowflg: boolean;
  currentData: any;
  loading: boolean;
  randomData: any[];
  resultData: any[];
  winnerData: any[];
}

const gameSlice = createSlice({
  name: "app",
  initialState,
  reducers: {
    fetchAppSuccess(state, action) {
      setAll(state, action.payload);
      // console.log(action.payload);
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(loadGameDetails.pending, (state, action) => {
        state.loading = true;
      })
      .addCase(loadGameDetails.fulfilled, (state, action) => {
        setAll(state, action.payload);
        state.loading = false;
      })
      .addCase(loadGameDetails.rejected, (state, { error }) => {
        state.loading = false;
      });
  },
});

const baseInfo = (state: RootState) => state.app;

export default gameSlice.reducer;

export const { fetchAppSuccess } = gameSlice.actions;

export const getAppState = createSelector(baseInfo, (app) => app);
